html,
body {
  margin: 0;
}
body {
  padding: 0.5em;
  background-color: #55aaaa;
}
::selection {
  background-color: #0000aa;
  color: #fff;
}

h1#page-title,
p#tag-line {
  text-align: center;
  color: #fff;
  font-family: "MS Sans Serif", "Microsoft MS Sans Serif", "Sans Serif";
}

#container {
  display: flex;
  justify-content: space-around;
}

.title-bar {
  background-color: #0000aa;
  margin: 0 0 3px;
  color: #fff;
  padding: 3px;
  border: 1px solid #9194c5;
  font-weight: bold;
  font-family: "MS Sans Serif", "Microsoft MS Sans Serif", "Sans Serif";
  height: 19px;
  line-height: 19px;
}
.window-icon {
  background-color: #dddfe1;
  padding: 0 2px;
  margin: 0 2px;
  float: right;
  line-height: 18px;
  width: 14px;
  text-align: center;
}

#editor-container {
  display: flex;
  width: 30%;
  height: 40vh;
  flex-direction: column;
}

#editor {
  resize: none;
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#editor:focus {
  outline: none !important;
}

#preview,
#editor {
  width: 100%;
  height: 100%;
  padding: 1em;
  box-sizing: border-box;
  border-top: 2px solid #5d5f63;
  border-right: 1px solid #cbcfd2;
  border-bottom: 1px solid #cbcfd2;
  border-left: 2px solid #5d5f63;
  background-color: #fff;
}

#preview-container,
#editor-container {
  border-top: 2px solid #dddfe1;
  border-right: 2px solid #626165;
  border-bottom: 2px solid #626165;
  border-left: 2px solid #dddfe1;
  background-color: #dddfe1;
  padding: 2px;
  box-shadow: 5px 10px 10px #3c7777;
}

#preview-container {
  width: 60%;
}

#preview {
  overflow-y: scroll;
  height: 80vh;
  font-family: Times New Roman, serif;
}

#preview img {
  max-width: 100%;
}

#preview table {
  border: 1px solid grey;
}
#preview th,
#preview td {
  border: 1px solid grey;
}

#preview pre {
  display: block;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  background-color: #e0e2e3;
  display: inline-block;
  padding: 0.5em;
}

.maximized {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw !important;
  height: 100vh !important;
  box-sizing: border-box;
}

.maximized #preview {
  height: 97%;
}

@media only screen and (max-width: 600px) {
  body {
    font-size: 0.85em;
    padding: 1em 0.2em;
  }
  h1#page-title {
    margin: 0;
  }
  #editor {
    padding: 0.5em;
  }
  #preview {
    padding: 0 0.5em;
  }
}
